import * as GW from 'gw-utils';
export const NOTHING = 0;
export const FLOOR = 1;
export const DOOR = 2;
export const WALL = 3;
export const LAKE = 4;
export const BRIDGE = 5;
export const UP_STAIRS = 6;
export const DOWN_STAIRS = 7;
export const SHALLOW = 8;
export function fillCostGrid(source, costGrid) {
    source.forEach((_v, x, y) => {
        costGrid[x][y] = isPassable(source, x, y) ? 1 : GW.path.OBSTRUCTION;
    });
}
export function isPassable(grid, x, y) {
    const v = grid.get(x, y);
    return (v === FLOOR ||
        v === DOOR ||
        v === BRIDGE ||
        v === UP_STAIRS ||
        v === DOWN_STAIRS ||
        v === SHALLOW);
}
export function isDoor(grid, x, y) {
    const v = grid.get(x, y);
    return v === DOOR;
}
export function isObstruction(grid, x, y) {
    const v = grid.get(x, y);
    return v === NOTHING || v === WALL;
}
export function isStairs(grid, x, y) {
    const v = grid.get(x, y);
    return v === UP_STAIRS || v === DOWN_STAIRS;
}
export function isLake(grid, x, y) {
    return grid.get(x, y) === LAKE;
}
export function isAnyWater(grid, x, y) {
    const v = grid.get(x, y);
    return v === LAKE || v === SHALLOW;
}
